<template>
	<view>
		<!-- logo -->
		<logo />
		<!-- 中英文选择 -->
		<language-form @languageComfirmed="languageComfirmed">
			<template #confirmText>{{ i18n.selected }}</template>
		</language-form>
		<!-- 无网络提示 -->
		<u-no-network :tips="i18n.lossNetwork"></u-no-network>
	</view>
</template>

<script>
	import Logo from '@/components/Logo/Logo.vue'
	import LanguageForm from '@/components/LanguageForm/LanguageForm.vue'
	export default {
		components: {
			Logo,
			LanguageForm
		},
		methods: {
			// 点击选好了按钮触发
			languageComfirmed() {
				// 底部的 tabbar 语言切换
				uni.setTabBarItem({ index: 0, text: this.$t('tabbar.index')})
				uni.setTabBarItem({ index: 1, text: this.$t('tabbar.order')})
				uni.setTabBarItem({ index: 1, text: this.$t('tabbar.mine')})
				// 页面跳转
				// 登录判断
				const token = uni.getStorageSync('token')
				if (!token) {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				} else {
					uni.navigateTo({
						url: '/pages/index/index'
					})
				}
			}
		},
		computed: {
			// 国际化
			i18n() {
				return this.$t('language')
			}
		}
	}
</script>

<style lang="scss" scoped>
</style>
